import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardActions, CardMenu, CardText } from 'react-mdl';

class Project extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }

    toggleCategories() {
        if (this.state.activeTab === 0) {
            return (
                <div className="projects-grid">
                    {/* // -------------------------PROJECT---1------------------------------------------------------------------------ */}
                    <Card shadow={0} style={{ width: '500px', margin: 'auto', marginTop: '10px' }}>
                        <CardTitle style={{ color: '#fff', height: '155px', content: 'center', background: 'url(data:image/png;base64,) center / cover' }}>Travel-Buddy</CardTitle>
                        <CardText>
                            Front End
                        <br></br>
                            <br></br>
                        This easy to use application will help you get around your desired vacation city without bumps in the road,
                        upon user input top 3 suggestions for lodging, restaurants, tourist attraction will show including google map.
                        </CardText>
                        <CardActions border>
                            <a href="https://mugen430.github.io/travel-buddy/" target="blank">Project Link</a>
                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>
                        </CardMenu>
                    </Card>
                    {/* -------------------------PROJECT---2------------------------------------------------------------------------ */}
                    <Card shadow={0} style={{ width: '500px', margin: 'auto', marginTop: '10px' }}>
                        <CardTitle style={{ color: '#fff', height: '155px', content: 'center', background: 'url(data:image/jpeg;base64,' }}>NOTEPAD--</CardTitle>
                        <CardText>
                            The all-in-one resource organizer for students and researchers. Here the user can take look up articles and take notes on them while being able to save it all in one place.
                            No more copying down URLs, just bookmark the page and the notes will be saved with them.
                        </CardText>
                        <CardActions border>
                            <a href="https://oscarcat.herokuapp.com/" target="blank">Project Link</a>
                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>
                        </CardMenu>
                    </Card>
                    {/* -------------------------BPTX------------------------------------------------------------------------ */}
                    <Card shadow={0} style={{ width: '500px', margin: 'auto', marginTop: '10px' }}>
                        <CardTitle style={{ color: '#00000', height: '155px', content: 'center', background: 'url(data:image/jpeg;base64,' }}>BIMMER PARTS TX</CardTitle>
                        <CardText>
                            Full-Stack Website made for a client speicilizing in BMW Parts.
                        </CardText>
                        <CardActions border>
                            <a href="https://www.bimmerpartstx.com/home" target="blank">Live Link</a>
                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>
                        </CardMenu>
                    </Card>
                    {/* -------------------------Houston Jewelers------------------------------------------------------------------------ */}
                    <Card shadow={0} style={{ width: '500px', margin: 'auto', marginTop: '10px' }}>
                        <CardTitle style={{ color: '#00000', height: '155px', content: 'center', background: 'url(data:image/jpeg;base64,' }}>HOUSTON JEWELERS</CardTitle>
                        <CardText>
                            Full-Stack High-end Jewelry website made for a client.
                        </CardText>
                        <CardActions border>
                            <a href="http://hj-predeploy.s3-website-us-west-1.amazonaws.com/" target="blank">Wireframe Link</a>
                            <br></br>
                            <a href="https://www.houstonsjeweler.com/" target="blank">Live Link</a>
                        </CardActions>
                        <CardMenu style={{ color: '#fff' }}>
                        </CardMenu>
                    </Card>
                </div>
            )
        } else if (this.state.activeTab === 1) {
            return (
                <Card shadow={0} style={{ width: '500px', margin: 'auto', marginTop: '10px' }}>
                    <CardTitle style={{ color: '#fff', height: '155px', background: 'url(data:image/png;base64,) center / cover' }}>Repository</CardTitle>
                    <CardText>
                        My Github Repo
                        </CardText>
                    <CardActions border>
                        <a href="https://github.com/mugen430" target="blank">Repo Link</a>
                    </CardActions>
                    <CardMenu style={{ color: '#fff' }}>
                    </CardMenu>
                </Card>
            )
        }
    }

    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabID) => this.setState({ activeTab: tabID })} ripple >
                    <Tab>Projects</Tab>
                    <Tab>Repository</Tab>
                </Tabs>
                <Grid>
                    <Cell col={12}>
                        {this.toggleCategories()}
                    </Cell>
                </Grid>
            </div >
        )
    }
}

export default Project;