import React, { Component } from 'react';
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';

class About extends Component {
  render() {
    return (
      <div className="contact-body">
        <Grid className="contact-grid">
          <Cell col={6}>
            <h2>Maverick Acampado</h2>
            <img
              src={require('./img1.png')}
              alt="avatar"
            />
            <p>As a Full Stack Developer who's eager to gain more knowledge and put said knowledge to work, I aspire to be the best version of myself everyday,
            A father to a beautiful little girl, and a husband to an amazing woman, it has always been my dream to become a Software Engineer and build anything with my own two hands to the best extent I can do,
            I took the leap and went above and beyond to become a Software Engineer through Rice University so I can pave my path to succeeding and accomplishing my dreams to be able to provide a better life for my growing family, and do what I've always loved to do.
             </p>

          </Cell>
          <Cell col={6}>
            <h2>Contact Me</h2>
            <hr />

            <div className="contact-list">
              <List>
                <ListItem>
                  <ListItemContent>
                    <i className="fa fa-linkedin" aria-hidden="true" />
                    <a href="https://www.linkedin.com/in/maverick-acampado-765a171a8/" target="blank" style={{ color: "#51A0CF" }}>linkedin</a>
                  </ListItemContent>
                </ListItem>

                <ListItem>
                  <ListItemContent>
                    <i className="fa fa-github" aria-hidden="true" />
                    <a href="https://github.com/mugen430" target="blank" style={{ color: "#51A0CF" }}>github</a>
                  </ListItemContent>
                </ListItem>

                <ListItem>
                  <ListItemContent>
                    <i className="fa fa-envelope-open-o" aria-hidden="true" />
                    <a href="mailto:acampado.maverick@gmail.com" target="blank" style={{ color: "#51A0CF" }}>Email Me!</a>
                  </ListItemContent>
                </ListItem>

                <ListItem>
                  <ListItemContent>
                    <i className="fa fa-envelope" aria-hidden="true" />
                    acampado.maverick@gmail.com
                  </ListItemContent>
                </ListItem>
              </List>
            </div>
          </Cell>
        </Grid>
      </div >
    )
  }
}

export default About;